* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-image: linear-gradient(to bottom, hsl(273deg, 75%, 66%) 0%, hsl(240deg, 73%, 65%) 100%);
  font-family: "Kumbh Sans", sans-serif;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
}

.wrapper {
  position: relative;
  grid-gap: 20px;
  background-color: hsl(0deg, 0%, 100%);
  background-image: url(../img/bg-pattern-desktop.svg);
  background-position: -387px -198px;
  background-repeat: no-repeat;
  background-size: 761px;
  border-radius: 30px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  max-width: 880px;
  width: 90%;
}
.wrapper__image--inner {
  background-image: url(../img/illustration-woman-online-desktop.svg);
  background-position: -78px 62px;
  background-repeat: no-repeat;
  background-size: 430px;
  height: 100%;
}

@media (max-width: 768px) {
  .wrapper {
    background-image: url(../img/bg-pattern-mobile.svg);
    background-position: 43% 0;
    background-size: 250px;
    grid-template-columns: unset;
    grid-template-rows: 1fr 4fr;
    padding: 0 30px 40px 30px;
  }
}
@media (max-width: 768px) {
  .wrapper__image--inner {
    background-image: none;
  }
}
.box {
  bottom: 120px;
  left: -127px;
  position: absolute;
}

.box2 {
  position: absolute;
  bottom: 85%;
  left: 15%;
  width: 230px;
}

@media (max-width: 768px) {
  .box {
    display: none;
  }
}
@media (min-width: 769px) {
  .box2 {
    display: none;
  }
}
.arrow {
  margin-right: 15px;
}

@media (max-width: 769px) {
  .arrow {
    margin: 5px;
  }
}
.accordion {
  padding: 70px 70px 70px 0;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.accordion__questions > .answer {
  display: none;
}
.accordion__questions {
  border-bottom: 1px solid, rgba(120, 120, 135, 0.1);
}
.accordion__title {
  margin-bottom: 20px;
}

@media (max-width: 768px) {
  .accordion {
    padding: 0;
  }
  .accordion__title {
    text-align: center;
    margin-block: 17px;
  }
}
.question {
  font-weight: 400;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0;
}
.question__title {
  font-size: 16px;
  font-weight: 400;
  color: hsl(240deg, 6%, 50%);
}
.question__title:hover {
  color: hsl(14deg, 88%, 65%);
  cursor: pointer;
}

.activo > .answer {
  font-weight: 400;
  color: hsl(240deg, 6%, 50%);
  font-size: 12px;
  display: block;
  padding-bottom: 10px;
}

.activo .question__title {
  font-weight: 700;
  color: hsl(238deg, 29%, 16%);
}

.activo .arrow {
  transform: rotate(180deg);
  transition: all 0.3s ease-in;
}